import {
  Streamlit,
  StreamlitComponentBase,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, { ReactNode,Component } from "react"
import Charty from 'react-charty'

interface State {
  numClicks: number
  isFocused: boolean
}


class Streamlit_charty extends StreamlitComponentBase<State> {
  public state = { numClicks: 0, isFocused: false }

  public render = (): ReactNode => {

    const options = this.props.args["opt"]
    

    return (
      <span>
      <Charty disabled={this.props.disabled} title="" {...options} />
      </span>
    )
  }

}

export default withStreamlitConnection(Streamlit_charty)
